import Vue from 'vue'
import store from './store'


function newRNG(seed) {
  // LCG using GCC's constants
  this.m = 0x80000000; // 2**31;
  this.a = 1103515245;
  this.c = 12345;

  this.state = seed ? seed : Math.floor(Math.random() * (this.m - 1));
}
newRNG.prototype.nextInt = function() {
  this.state = (this.a * this.state + this.c) % this.m;
  return this.state;
}
newRNG.prototype.nextFloat = function() {
  // returns in range [0,1]
  return this.nextInt() / (this.m - 1);
}
newRNG.prototype.nextRange = function(start, end) {
  // returns in range [start, end): including start, excluding end
  // can't modulu nextInt because of weak randomness in lower bits
  var rangeSize = end - start;
  var randomUnder1 = this.nextInt() / this.m;
  return start + Math.floor(randomUnder1 * rangeSize);
}
newRNG.prototype.choice = function(array) {
  return array[this.nextRange(0, array.length)];
}
Vue.prototype.newRNG = newRNG;

String.prototype.hashCode = function() {
  var hash = 0, i, chr;
  if (this.length === 0) return hash;
  for (i = 0; i < this.length; i++) {
    chr   = this.charCodeAt(i);
    hash  = ((hash << 5) - hash) + chr;
    hash |= 0; // Convert to 32bit integer
  }
  return hash;
};

let SOLUTIONS = {name: 'Solutions', tag: 'Sols'}
let HASHES = {name: 'Hashes', tag: 'H'}

Vue.prototype.getHashingType = function(algorithm) {
  if(algorithm == 'Equihash') return SOLUTIONS;
  return HASHES
}

Vue.prototype.convertTemperature = function(kelvin, display=false) {
  let ret = [kelvin, 'K'];

  if(kelvin === undefined || kelvin == 0) {
    return !display ? [0, null] : 'N/A';
  }else if(localStorage.temperature != 'K') {
    if(localStorage.temperature == 'C') ret = [kelvin - 273.15, 'C'];
    if(localStorage.temperature == 'F') ret = [(kelvin - 273.15) * 1.8 + 32, 'F'];
  }

  return !display ? ret : ret[0].toFixed(0) + ' °' + ret[1];
};

Vue.prototype.convertRate = function({ rate, display, minimum, algorithm }) {
  rate = parseFloat(rate || 0);

  let hType = this.getHashingType(algorithm);
  let ret = [rate, ''];

  if(rate === undefined) {
    return !display ? [0, ''] : 'N/A';
  }else{
    if(minimum == 'T'      || rate / 5000000000000 > 1) ret = [rate / 1000000000000, 'T'];
    else if(minimum == 'G' || rate / 5000000000 > 1) ret = [rate / 1000000000, 'G'];
    else if(minimum == 'M' || rate / 5000000 > 1) ret = [rate / 1000000, 'M'];
    else if(minimum == 'k' || rate / 5000 > 1) ret = [rate / 1000, 'k'];
  }

  if(!display) return ret;

  ret[1] += hType.tag;

  if(parseFloat(ret[0].toFixed(0)) != parseFloat(ret[0].toFixed(2)))
    ret[0] = ret[0].toFixed(2);
  else
    ret[0] = ret[0].toFixed(0);

  return ret[0] + ' ' + ret[1];
};


Vue.prototype.doGroupAction = function(group, action) {
  let v = {to: 'one'}; v[group] = {id: action};
  Vue.prototype.$socket.send('groups', 'action', v);
};

Vue.prototype.doMinerAction = function(miner, action) {
  let v = {to: 'one'}; v[miner] = {id: action};
  Vue.prototype.$socket.send('machines', 'action', v);
};


Vue.prototype.getGPUStats = function(opt={}) {
  let rates = {};

  for(let [j, machine] of Object.entries(this.machines)) {
    if(!machine.config) continue;
    if(!machine.config.group) continue;
    if(opt.group && miner.config.group.id != opt.group) continue;
    
    let group = this.groups[machine.config.group.id];
    if(!group) continue;

    if(!group.pool || !this.pools.hasOwnProperty(group.pool.id)) continue;

    let pool = this.pools[group.pool.id].endpoints[group.pool.endpoint];
    if(!pool) continue;

    let wallet = this.wallets[group.wallet];

    let crypto = wallet && wallet.crypto ? wallet.crypto : pool.crypto;

    if(!this.miner_stats[j]) continue;

    let rate = 0;
    let watts = 0;
    for(let [k, gpu] of Object.entries(this.miner_stats[j].hardware.gpus)) {
      rate += gpu.rate;
      watts += gpu.watts;
    }

    if(!rates.hasOwnProperty(crypto))
      rates[crypto] = { rate: 0, watts: 0 };
    rates[crypto].rate += rate;
    rates[crypto].watts += watts;
  }

  return rates;
};

Vue.prototype.getDailyEstimates = function(opt={}) {
  let earnings = {}; // Per minute

  for(let [crypto, data] of Object.entries(this.getGPUStats(opt))) {
    let rate = 0;
    if(data instanceof Object)
      rate = data.rate;
    else
      rate = data;

    let coin = this.coins[crypto];
    if(!coin) continue;

    let blockTime = parseInt(coin.block_time);
    let netHash = parseInt(coin.nethash);
    let ethPerMin = (60 / blockTime) * coin.block_reward;

    let userRatio = rate / netHash;

    if(!earnings.hasOwnProperty(crypto))
      earnings[crypto] = 0;
    earnings[crypto] += (userRatio * ethPerMin) * coin.exchange_rate * 60 * 24;
  }

  return earnings;
};

Vue.prototype.convertBTC = function(opt={}) {
  if(!opt.to) opt.to = this.$store.state.currency;

  let amt = this.fromBTC(opt);

  if(this.tickers[opt.to]) {
    return amt.toFixed(2) + ' ' + opt.to;
  }else
    return amt.toFixed(8) + ' BTC';
};

Vue.prototype.fromBTC = function(opt={}) {
  if(!opt.to) opt.to = this.$store.state.currency;

  let total = 0;

  if(opt.btc instanceof Object)
    for(let amt of Object.values(opt.btc))
      total += amt;
  else
    total += opt.btc;
  
  if(opt.mul) total *= opt.mul;

  if(this.tickers[opt.to]) {
    return total * this.tickers[opt.to].sell;
  }else
    return total;
};

Vue.prototype.toBTC = function(opt={}) {
  if(!opt.from) opt.from = this.$store.state.currency;

  let total = 0;

  if(opt.amt instanceof Object)
    for(let amt of Object.values(opt.amt))
      total += amt;
  else
    total += opt.amt;
  
  if(opt.mul) total *= opt.mul;

  if(this.tickers[opt.from]) {
    return total / this.tickers[opt.from].sell;
  }else
    return null;
};


Vue.prototype.isSynced = function(group_id, miner) {
  if(miner.config.dummy) return true;

  if(!miner.config) return false;
  if(!miner.config.group) return false;

  if(group_id != miner.config.group.id) return false;

  let group = store.state.groups[group_id];
  if(!group) return false;

  if(group.wallet) {
    if(!miner.config.wallet) return false;
    if(!store.state.wallets.hasOwnProperty(group.wallet)) return false;
    if(store.state.wallets[group.wallet].address != miner.config.wallet.address) return false;
  }else
    if(miner.config.wallet) return false;

  if(group.pool) {
    if(!miner.config.pool.endpoint) return false;
    if(!store.state.pools.hasOwnProperty(group.pool.id)) return false;

    let pool = store.state.pools[group.pool.id];
    if(!pool.endpoints.hasOwnProperty(group.pool.endpoint)) return false;

    let endpoint = pool.endpoints[group.pool.endpoint];
    if(endpoint.url != miner.config.pool.endpoint.url) return false;
  }

  if(group.program) {
    if(!miner.config.program) return false;

    if(group.program.execute.args != miner.config.program.execute.args) return false;

    if(!store.state.software.hasOwnProperty(group.program.id)) return false;

    let program = store.state.software[group.program.id];
    if(program.name != miner.config.program.name) return false;
  }

  return true;
};

Vue.prototype.getOnlineMiners = function(opt={}) {
  let t = 0;
  for(let id in store.state.machines) {
    let miner = store.state.machines[id];
    if(opt.group) {
      if(!miner.config) continue;
      if(!miner.config.group) continue;
      if(miner.config.group.id != opt.group) continue;
    }

    let stats = store.state.miner_stats[id];
    if(!stats) continue;

    t += stats && stats.status.type != 'offline' ? 1 : 0;
  }
  return t;
};

Vue.prototype.getUnsyncedMiners = function(opt={}) {
  let t = 0;
  for(let id in store.state.machines) {
    let miner = store.state.machines[id];
    
    if(!miner.config) continue;
    if(!miner.config.group) continue;
    if(opt.group)
      if(miner.config.group.id != opt.group) continue;

    t += this.isSynced(miner.config.group.id, miner) ? 0 : 1;
  }
  return t;
};

Vue.prototype.getOfflineMiners = function(opt={}) {
  let t = 0;
  for(let id in store.state.machines) {
    let miner = store.state.machines[id];
    if(opt.group) {
      if(!miner.config) continue;
      if(!miner.config.group) continue;
      if(miner.config.group.id != opt.group) continue;
    }

    let stats = store.state.miner_stats[id];

    t += !stats || stats.status.type != 'offline' ? 0 : 1;
  }
  return t;
};

Vue.prototype.getDisconnectedMiners = function(opt={}) {
  let t = 0;
  for(let id in store.state.machines) {
    let miner = store.state.machines[id];
    if(opt.group) {
      if(!miner.config) continue;
      if(!miner.config.group) continue;
      if(miner.config.group.id != opt.group) continue;
    }

    let stats = store.state.miner_stats[id];

    t += stats && stats.connected ? 0 : 1;
  }
  return t;
};

Vue.prototype.getTotalMiners = function(opt={}) {
  let t = 0;
  for(let id in store.state.machines) {
    let miner = store.state.machines[id];
    if(opt.group) {
      if(!miner.config) continue;
      if(!miner.config.group) continue;
      if(miner.config.group.id != opt.group) continue;
    }

    t += 1;
  }
  return t;
};


Vue.prototype.getMaxTemperature = function(opt={}) {
  let t = 0;
  if(!opt.miner) {
    for(let id in store.state.machines) {
      let miner = store.state.machines[id];
      if(opt.group) {
        if(!miner.config) continue;
        if(!miner.config.group) continue;
        if(miner.config.group.id != opt.group) continue;
      }

      let stats = store.state.miner_stats[id];
      if(!stats || !stats.hardware || !stats.hardware.gpus) continue;

      for(let j in stats.hardware.gpus)
        if(stats.hardware.gpus[j].temp > t)
          t = stats.hardware.gpus[j].temp;
    }
  }else{
    let stats = store.state.miner_stats[opt.miner];
    if(!stats || !stats.hardware || !stats.hardware.gpus) return 0;

    for(let i in stats.hardware.gpus)
      if(stats.hardware.gpus[i].temp > t)
        t = stats.hardware.gpus[i].temp;
  }
  return t;
};

Vue.prototype.getTotalWatts = function(opt={}) {
  let t = 0;
  if(!opt.miner) {
    for(let id in store.state.machines) {
      let miner = store.state.machines[id];
      if(opt.group) {
        if(!miner.config) continue;
        if(!miner.config.group) continue;
        if(miner.config.group.id != opt.group) continue;
      }

      let stats = store.state.miner_stats[id];
      if(!stats || !stats.hardware || !stats.hardware.gpus) continue;

      for(let j in stats.hardware.gpus)
        t += stats.hardware.gpus[j].watts;
    }
  }else{
    let stats = store.state.miner_stats[opt.miner];
    if(!stats || !stats.hardware || !stats.hardware.gpus) return 0;

    for(let i in stats.hardware.gpus)
      t += stats.hardware.gpus[i].watts;
  }
  return t;
};

Vue.prototype.getAlgorithm = function(opt={}) {
  if(opt.miner) {
    let machine = store.state.machines[opt.miner];
    if(!machine.config.group) return null;

    let group = store.state.groups[machine.config.group.id];
    if(!group) return null;

    return group.algorithm;
  }else if(opt.group) {
    let group = store.state.groups[opt.group];
    if(!group) return null;
    
    return group.algorithm;
  }

  return null;
};

