<template>
  <v-container fluid grid-list-md>
    <v-layout row wrap>
      <v-flex xs12>
        <v-card>
          <v-card-text>
            <v-layout row wrap>
              <v-flex xs12 class="text-xs-center">
                <h2 class="mb-0 headline">
                  <div class="mx-2 mb-2" style="display:inline-block" v-for="(interval, i) in intervals" :key="i">
                    {{ interval.name }}: <code>{{ convertBTC({ btc: dailyEstimates, mul: interval.mul }) }}</code>
                  </div>
                </h2>
              </v-flex>
            </v-layout>
            <v-divider class="my-2" />
            <v-layout row wrap>
              <v-flex xs12 sm7>
                <v-layout row wrap class="mb-3" v-for="(interval, i) in intervals" :key="i">
                  <v-flex xs5>
                    {{ interval.name }} Estimates
                  </v-flex>
                  <v-flex xs2 class="text-xs-right">
                    <small>Gross</small>
                  </v-flex>
                  <v-flex xs2 class="text-xs-right">
                    <small>Cost</small>
                  </v-flex>
                  <v-flex xs3 class="text-xs-right">
                    <small>Net</small>
                  </v-flex>
                  <template v-for="(amt, crypto) in dailyEstimates">
                    <v-container class="my-2" :key="'crypto-' + crypto">
                      <v-divider class="mb-2" />
                      <v-layout row wrap class="mx-2">
                        <v-flex xs5>
                          [{{ crypto }}] <b>{{ coins[crypto].name }}</b> <small>{{ coins[crypto].algorithm }}</small>
                        </v-flex>
                        <v-flex xs2 class="text-xs-right">
                          <small>{{ convertBTC({ btc: amt, mul: interval.mul }) }}</small>
                        </v-flex>
                        <v-flex xs2 class="text-xs-right">
                          <v-tooltip top>
                            <div slot="activator"><small>{{ convertBTC({ btc: getIntervalCost(interval, crypto, amt) }) }}</small></div>
                            {{ ((allGPUStats[crypto].watts / 1000) * 24 * interval.mul).toFixed(2) }} kWh
                          </v-tooltip>
                        </v-flex>
                        <v-flex xs3 class="text-xs-right">
                          <b :style="'color:' + (amt * interval.mul > getIntervalCost(interval, crypto, amt) ? 'green' : 'red')">
                            {{ convertBTC({ btc: amt * interval.mul - getIntervalCost(interval, crypto, amt) }) }}
                          </b>
                        </v-flex>
                      </v-layout>
                    </v-container>
                  </template>
                </v-layout>
              </v-flex>
              <v-flex xs12 sm5>
                <div style="display:block;height:300px">
                  <vue-chart
                    type="doughnut"
                    :data="chartData"
                    :options="{
                      legend: {
                        display: false
                      },
                      tooltips: {
                        callbacks: {
                          label: function(item, data) {
                            let dataset = data.datasets[item.datasetIndex];
                            return data.labels[item.index] + ': ' + convertBTC({ btc: dataset.data[item.index] });
                          }
                        }
                      }
                    }" />
                </div>
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    data() {
      return {
        intervals: [{
          name: 'Daily',
          mul: 1
        }, {
          name: 'Weekly',
          mul: 7
        }, {
          name: 'Monthly',
          mul: 30
        }]
      };
    },
    computed: {
      coins() {
        return this.$store.state.coins;
      },
      tickers() {
        return this.$store.state.tickers;
      },

      wallets() {
        return this.$store.state.wallets;
      },

      groups() {
        return this.$store.state.groups;
      },
      pools() {
        return this.$store.state.pools;
      },
      machines() {
        return this.$store.state.machines;
      },
      miner_stats() {
        return this.$store.state.miner_stats;
      },

      costPerkWh() {
        return this.$store.state.kwh;
      },

      costPerkWh_BTC() {
        return this.toBTC({ from: this.$store.state.currency, amt: this.costPerkWh });
      },

      allGPUStats() {
        return this.getGPUStats();
      },
      dailyEstimates() {
        return this.getDailyEstimates();
      },

      chartData() {
        let labels = [];
        let color = [];
        let data = [];
        
        for(let [crypto, amt] of Object.entries(this.dailyEstimates)) {
          labels.push(crypto);
          color.push(this.getRandomColor(new this.newRNG(crypto.hashCode())));
          data.push(amt);
        }

        return {
          datasets: [
            {
              data,
              backgroundColor: color
            }
          ],
          labels
        };
      }
    },
    methods: {
      getRandomColor(rng) {
        var letters = '456789ABCD';
        var color = '#';
        for (var i = 0; i < 6; i++)
          color += letters[Math.floor(rng.nextFloat() * letters.length)];
        return color;
      },
      getIntervalCost(interval, crypto, amt) {
        return amt * interval.mul - ((this.allGPUStats[crypto].watts / 1000) * 24 * interval.mul * this.costPerkWh_BTC)
      }
    }
  }
</script>



