<template>
  <v-container fluid grid-list-md>
    <v-layout row wrap>
      <v-flex xs12>
        <v-card>
          <v-card-text>
            <v-flex xs12>
              Hashrate Breakdown
            </v-flex>
            <v-layout row wrap>
              <v-flex xs12 sm6>
                <template v-for="(stats, crypto) in allGPUStats">
                  <v-container class="my-2" :key="'crypto-' + crypto">
                    <v-divider class="mb-2" />
                    <v-layout row wrap class="mx-2">
                      <v-flex xs6>
                        <b>{{ coins[crypto].name }}</b> <small>{{ coins[crypto].algorithm }}</small>
                      </v-flex>
                      <v-flex xs6 class="text-xs-right">
                        {{ convertRate({ rate: stats.rate, display: true, algorithm: coins[crypto].algorithm }) }}
                      </v-flex>
                    </v-layout>
                  </v-container>
                </template>
              </v-flex>
              <v-flex xs12 sm6>
                <div style="display:block;height:300px">
                  <vue-chart
                    type="doughnut"
                    :data="chartData"
                    :options="{
                      legend: {
                        display: false
                      },
                      tooltips: {
                        callbacks: {
                          label: function(item, data) {
                            let dataset = data.datasets[item.datasetIndex];
                            return data.labels[item.index] + ': ' + convertRate({ rate: allGPUStats[data.labels[item.index]].rate, display: true, algorithm: coins[data.labels[item.index]].algorithm });
                          }
                        }
                      }
                    }" />
                </div>
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    computed: {
      coins() {
        return this.$store.state.coins;
      },

      wallets() {
        return this.$store.state.wallets;
      },

      groups() {
        return this.$store.state.groups;
      },
      pools() {
        return this.$store.state.pools;
      },
      machines() {
        return this.$store.state.machines;
      },
      miner_stats() {
        return this.$store.state.miner_stats;
      },

      allGPUStats() {
        return this.getGPUStats();
      },
      dailyEstimates() {
        return this.getDailyEstimates();
      },

      chartData() {
        let labels = [];
        let color = [];
        let data = [];
        let rate = [];
        
        for(let [crypto, amt] of Object.entries(this.dailyEstimates)) {
          labels.push(crypto);
          color.push(this.getRandomColor(new this.newRNG(crypto.hashCode())));
          data.push(amt);
        }

        return {
          datasets: [
            {
              data,
              rate,
              backgroundColor: color
            }
          ],
          labels
        };
      }
    },
    methods: {
      getRandomColor(rng) {
        var letters = '456789ABCD';
        var color = '#';
        for (var i = 0; i < 6; i++)
          color += letters[Math.floor(rng.nextFloat() * letters.length)];
        return color;
      }
    }
  }
</script>



