<template>
  <div>
    <v-container fluid grid-list-md>
      <v-layout row wrap>
        <v-flex md6 lg3>
          <router-link tag="div" style="cursor:pointer" :to="{ name: 'Hashrates' }">
            <v-card>
              <v-card-text>
                <v-layout row wrap>
                  <v-flex md4>
                    <v-icon color="black" style="font-size:4rem">fa-tachometer</v-icon>
                  </v-flex>
                  <v-flex md8>
                    <b>Hashrate</b>
                    <v-divider class="my-2"></v-divider>
                    {{ convertRate({ rate: getTotalRate(), display: true }) }}/s
                  </v-flex>
                </v-layout>
              </v-card-text>
            </v-card>
          </router-link>
        </v-flex>
        <v-flex md6 lg3>
          <router-link tag="div" style="cursor:pointer" :to="{ name: 'Daily Estimates' }">
            <v-card>
              <v-card-text>
                <v-layout row wrap>
                  <v-flex md4>
                    <v-icon color="green" style="font-size:4rem">fa-money</v-icon>
                  </v-flex>
                  <v-flex md8>
                    <b>Daily Estimate</b>
                    <v-divider class="my-2"></v-divider>
                    {{ convertBTC({ btc: getDailyEstimates() }) }}
                  </v-flex>
                </v-layout>
              </v-card-text>
            </v-card>
          </router-link>
        </v-flex>
        <v-flex md6 lg3>
          <router-link tag="div" style="cursor:pointer" :to="{ name: 'Machines' }">
            <v-card>
              <v-card-text>
                <v-layout row wrap>
                  <v-flex md4>
                    <v-icon color="blue-grey" style="font-size:4rem">fa-database</v-icon>
                  </v-flex>
                  <v-flex md8>
                    <b>Miners</b>
                    <v-divider class="my-2"></v-divider>
                    {{ getOnlineMiners() }}/{{ getTotalMiners() }}
                  </v-flex>
                </v-layout>
              </v-card-text>
            </v-card>
          </router-link>
        </v-flex>
        <v-flex md6 lg3>
          <router-link tag="div" style="cursor:pointer" :to="{ name: 'Health Center' }">
            <v-card>
              <v-card-text>
                <v-layout row wrap>
                  <v-flex md4>
                    <v-icon class="success--text" style="font-size:4rem" v-if="badOnes == 0">check</v-icon>
                    <v-icon class="error--text" style="font-size:4rem" v-else>close</v-icon>
                  </v-flex>
                  <v-flex md8>
                    <b>Health</b>
                    <v-divider class="my-2"></v-divider>
                    {{ badOnes }} Warnings
                  </v-flex>
                </v-layout>
              </v-card-text>
            </v-card>
          </router-link>
        </v-flex>
      </v-layout>
      <v-layout row wrap>
        <v-flex md7>
          <statistics
              title="Rate"
              info="The total hashrate of your farm"
              id="overview-rate" type="rate" />
        </v-flex>
        <v-flex md5>
          <statistics
            class="mb-2"
            title="Accepted Shares"
            info="The number of shares accepted over time"
            id="overview-accepted" type="accepted" />
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
  export default {
    computed: {
      coins() {
        return this.$store.state.coins;
      },
      tickers() {
        return this.$store.state.tickers;
      },
      messages() {
        return this.$store.state.messages;
      },

      wallets() {
        return this.$store.state.wallets;
      },

      groups() {
        return this.$store.state.groups;
      },
      pools() {
        return this.$store.state.pools;
      },
      machines() {
        return this.$store.state.machines;
      },
      miner_stats() {
        return this.$store.state.miner_stats;
      },

      badOnes() {
        let t = 0;
        for(let [i, message] of Object.entries(this.messages))
          t += message.level == 'bug' || message.level == 'warning' || message.level == 'danger';
        return t;
      }
    }
  }
</script>



